const { Component } = React

const PINK = '#ff7575'

const BigNumber = ({ children }) => 
  <div className="big-number">
    {children}
    <style jsx>{`
      .big-number {
        font-family: Helvetica;
        font-size: 20rem;
        color: white;
        position: absolute;
        bottom: 0;
        left: 0;
      }
    `}</style>
  </div>

const Button = ({ children }) =>
  <button>
    {children}
    <style jsx>{`
      padding: 1rem;
      font-size: 2rem;
      font-family: Helvetica;
      color: ${PINK}
    `}</style>
  </button>

class Page extends Component {
  
  state = { scrollPosition: 0 } 

  componentDidMount() {
    this.startCounter()
  }

  startCounter = () => {
    this.counterId = setInterval(() => {
      this.setState(s => ({ scrollPosition: s.scrollPosition + 1 }))
    }, 1000)
  }

  stopCounter = () => {
    clearInterval(this.counterId)
  }
 
  render(){
    return ( 
      <div className="root">
        <BigNumber>{this.state.scrollPosition}</BigNumber>
        <Button>STOP</Button>
        <style jsx>{`
          .root {
            background: ${PINK} ;
            height: 100vh;
            width: 100vw;
            display: flex;
            justify-content: center;
            align-items: center;
            position: relative;
          }
        `}</style>
      </div>
    )
  }
}

export default Page



